  font-family: 'Metropolis-Regular';
  src: url('./Fonts/Metropolis-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Metropolis-SemiBold';
  src: url('./Fonts/Metropolis-SemiBold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Metropolis-ExtraBold';
  src: url('./Fonts/Metropolis-ExtraBold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Metropolis-Black';
  src: url('./Fonts/Metropolis-Black.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Metropolis-Bold';
  src: url('./Fonts/Metropolis-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Resolve-Light';
  src: url('./Fonts/Resolve-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

.hide-top-space {
  margin-top: -130px !important;
  @media screen and (max-width: 575px){
    margin-top: -180px !important;
  }
}

.authenticity-wrapper {
  background-color: #d3d3d3;
  background-image: url("../../assets/imgs/authenticity/authenticity-bg.png");
  background-size: contain;
  background-repeat: repeat-y;
  .authenticity-main {
    font-family: 'Metropolis-Medium', sans-serif;

    h2{
      font-family: 'Metropolis-Black', sans-serif;
    }

    .authenticity-banner {
      background-color: #404040;
      background-image: url("../../assets/imgs/authenticity/banner-shadow.png");
      background-size: cover;
      background-repeat: no-repeat;
      position: relative;
      p{
        font-family: 'Resolve-Light', sans-serif;
      }
    }

    .authenticity-who-we-are {
      background-color: #fff8ac;
      border-radius: 20px;

      .who-card {
        position: relative;

        img {
          width: 160px;
          height: 150px;
          object-fit: contain;

        }

        p {
          border-radius: 3px;
          padding: 5px 15px;
          margin: 12px 0;
          font-size: 12px;
          line-height: 18px;
          font-weight: bold;
          text-transform: uppercase;
          color: #000000;
          background-color: #ffd029;
        @media screen and (max-width: 575px){
          font-size: 10px;
        }
        }
      }

      .who-card-arrow {
        img {
          width: 20px;
          @media screen and (max-width: 767px){
            transform: rotate(90deg);
          }
        }
      }
    }

    .authenticity-how-to-check {
      background-color: #c8ffd6;
      border-radius: 20px;

      .how-check-card {
        background-color: #13464a;
        position: relative;
        height: 160px;
        width: 175px;
        padding: 15px 30px 15px 15px;
        display: flex !important;
        align-items: center;
        justify-content: center;
        margin: 0 auto;

        @media screen and (max-width: 425px){
          margin: 15px auto !important;
        }

        @media screen and (max-width: 767px){
          margin: 15px;
          width: 250px;
          height: 190px;
        }

        .card-number {
          position: absolute;
          top: -15px;
          left: 15px;
          border-radius: 50%;
          background-color: #fef834;
          height: 25px;
          width: 25px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 13px;
          font-weight: bold;
        }

        p {
          text-align: left;
          font-size: 10px;
          color: #ffffff;
          margin: 0;
          z-index: 0;
          @media screen and (max-width: 767px){
            font-size: 12px;
          }
        }

        .product-image {
          position: absolute;
          right: -50px;
          bottom: 10px;
          width: 80px;
          z-index: 1;
        }
      }

    }

    .questions-main{
      background-color: #ffbb24;
      background-image: url("../../assets/imgs/authenticity/question.png");
      background-size: 100% 100%;
      background-repeat: no-repeat;
      border-radius: 20px;
      position: relative;

      @media screen and (max-width: 576px){
        background-size: cover;
      }

      .question-accordian{
        .card-header{
          background-color: #f26522;
          border-radius: 3px;
          padding: 5px 15px;
          margin-bottom: 15px;
          font-size: 12px;
          font-family: 'Metropolis-Medium', sans-serif;
          button{
            color: #ffffff;
            white-space: normal;
            text-decoration: none;
            font-family: 'Metropolis-Medium', sans-serif;
            font-size: 14px;
          }

          button[aria-expanded="true"]{
            i{
              transform: rotate(-90deg) !important;
            }
          }
        }

        .card-body{
          padding-top: 0;
          font-size: 14px;
          font-family: 'Metropolis-Medium', sans-serif;
        }
      }

      .question-mark-image{
        position: absolute;
        @media screen and (max-width: 991px){
          top: -15px;
          left: -15px;
          img{
            width: 20%;
          }
        }

        @media screen and (min-width: 992px){
          top: -20px;
          left: -40px;
          img{
            width: 30%;
          }
        }
      }
    }

    .certificates-main{
      background-color: #565656;
      border-radius: 20px;

      .custom-nav-tabs{
        border: none;
        display: block;
        white-space: nowrap;
        padding-bottom: 10px;
        li{
          border: none;
          margin: 0 15px;
          display: inline-block;
          white-space: nowrap;
          a{
            background-color: #ffffff;
            font-weight: bold;
            font-size: 16px;
            color: #000000;
            text-transform: uppercase;
            border: none;
            border-radius: 0;
          }
          .active{
            background-color: #ffce24;
            color: #000000;
          }
        }
      }

      .image-gallery-slides {
        background: #f9f9f9;
        padding: 5px;
        border-radius: 10px;
      }

      .image-gallery-content img {
        max-height: 160px !important;
        height: 160px !important;
      }

      .tab-full-certificates {
        .image-gallery-content img {
          max-height: 500px !important;
          height: 500px !important;
          @media screen and (max-width: 576px) {
            max-height: 160px !important;
            height: 160px !important;
          }
        }
      }
      .image-gallery-fullscreen-button, .image-gallery-play-button {
        padding: 5px;
      }
      .image-gallery-fullscreen-button .image-gallery-svg, .image-gallery-play-button .image-gallery-svg {
        height: 22px;
        width: 22px;
      }

      .image-gallery-content.fullscreen img {
        max-width: inherit !important;
        max-height: calc(100vh - 80px) !important;
        height: auto !important;
      }

      .custom-nav-tabs::-webkit-scrollbar {
        width: 5px;
        height: 5px;
      }
      .custom-tab-content{
        .custom-tab-content-left{
          p{
            font-size: 12px;
            line-height: 18px;
            color: #ffffff;
            margin-bottom: 5px;
          }

          @media screen and (max-width: 767px){
            img{
              margin: 0 auto;
            }
          }
        }
      }
    }
  }
}
.image-gallery-left-nav .image-gallery-svg, .image-gallery-right-nav .image-gallery-svg {
  height: 50px !important;
  width: 60px !important;
}

.image-gallery {
  height: 100% !important;

  .image-gallery-swipe {
    height: 100%;
    @media screen and (max-width: 576px) {
      /*height: 400px;*/
      max-height: 400px;
    }

    @media screen and (max-width: 375px) {
      /*height: 370px;*/
      max-height: 370px;
    }

    .image-gallery-slides {
      height: 100%;
    }

    .image-gallery-slide {
      height: 100%;
    }
  }
}
